(function() {
  // jQuery
  $(function() {
    /* PANELE */
    var app, cookieConsent, forwardSwiperEl, infoObj, infoObjToggle, infoTemplate, infoTrigger, panels, scrollTo;
    forwardSwiperEl = $('.swiper-container.forward .swiper-wrapper');
    infoObj = {
      trg: $('section.info'),
      mode: 'collapsed',
      stage: 'none'
    };
    infoTrigger = {
      trg: infoObj.trg.find('.trigger button'),
      closeBtn: infoObj.trg.find('.layout button.closeBtn')
    };
    app = $('section.app');
    panels = {
      projects: infoObj.trg.find('article.projects'),
      projectsSidenav: infoObj.trg.find('article.projects nav.sidenav ul'),
      about: infoObj.trg.find('article.about'),
      aboutSidenav: infoObj.trg.find('article.about nav.sidenav ul')
    };
    infoObjToggle = function(target) {
      var targetObj;
      targetObj = infoObj.trg.find('.layout.' + target);
      if (infoObj.mode === 'expanded' && infoObj.stage === target) {
        infoObj.mode = 'collapsed';
        infoObj.stage = 'none';
        app.show();
        return infoObj.trg.animate({
          height: '0rem',
          paddingTop: 0
        }, 0, function() {
          targetObj.addClass('hidden');
          return infoObj.trg.find('.layout').hide();
        });
      } else if (infoObj.mode === 'expanded' && infoObj.stage !== target) {
        infoObj.trg.find('.layout.' + infoObj.stage).addClass('hidden').hide();
        targetObj.show();
        infoObj.stage = target;
        return setTimeout(function() {
          targetObj.removeClass('hidden');
          // targetObj.one('webkitTransitionEnd otransitionend oTransitionEnd msTransitionEnd transitionend', (e) ->
          // 	$(this).removeClass 'camo'
          // )
          return $.each(infoObj.trg.find(`article.${target} h2`), function(k, i) {
            $('.layout').scrollTop(0);
            return $(i).data('pos', Math.round($(i).offset().top - 160));
          });
        }, 100);
      } else {
        infoObj.mode = 'expanded';
        infoObj.stage = target;
        targetObj.show();
        return infoObj.trg.animate({
          height: window.innerHeight - 70
        }, 0, function() {
          app.hide();
          targetObj.removeClass('hidden');
          // targetObj.one('webkitTransitionEnd otransitionend oTransitionEnd msTransitionEnd transitionend', (e) ->
          // 	$(this).removeClass 'camo'
          // )
          return $.each(infoObj.trg.find(`article.${target} h2`), function(k, i) {
            $('.layout').scrollTop(0);
            return $(i).data('pos', Math.round($(i).offset().top - 160));
          });
        });
      }
    };
    infoTrigger.trg.click(function(e) {
      return infoObjToggle(e.target.classList.value);
    });
    infoTrigger.closeBtn.click(function(e) {
      return infoObjToggle(infoObj.stage);
    });
    $.getJSON('./ajax/slides.json', function(data) {
      var allFWImgs, allImgs, forwardSwiper, slides, swiperOptions;
      slides = [];
      $.each(data.slides, function(key, slide) {
        var slideTemplate;
        slideTemplate = `<div class="swiper-slide">
	<img src="images/${slide.image}_x1.png" alt="" srcset="images/${slide.image}_x1.png 1x, images/${slide.image}_x2.png 2x, images/${slide.image}_x3.png 3x">
	<div class="text-column">
		<p>${slide.description}</p>
	</div>
</div>`;
        forwardSwiperEl.append(slideTemplate);
        return slides.push(slide);
      });
      swiperOptions = {
        noSwipingClass: 'swiper-slide',
        loop: true,
        pagination: {
          type: 'fraction'
        }
      };
      forwardSwiper = new Swiper('.swiper-container.forward', {
        allowTouchMove: false,
        loop: true,
        pagination: {
          el: '.counter .forward-counter',
          type: 'fraction'
        },
        on: {
          transitionStart: function() {
            return $('.swiper-slide-active .text-column').hide();
          },
          transitionEnd: function() {
            return $('.swiper-slide-active .text-column').fadeIn();
          }
        }
      });
      $('.swiper-container .button.rew').click(function() {
        return forwardSwiper.slidePrev();
      });
      $('.swiper-container .button.ff').click(function() {
        return forwardSwiper.slideNext();
      });
      if ($(window).width() < 968) {
        forwardSwiper.allowTouchMove = true;
      }
      $(window).resize(function() {
        if ($(window).width() < 968) {
          return forwardSwiper.allowTouchMove = true;
        }
      });
      allImgs = $('.swiper-slide img'); // [TJNP]  chyba
      allFWImgs = $('.forward').find('.swiper-slide img');
      return $(document).keydown(function(e) {
        if (e.keyCode === 37) {
          return forwardSwiper.slidePrev();
        } else if (e.keyCode === 39) {
          return forwardSwiper.slideNext();
        }
      });
    });
    infoTemplate = function(item) {
      return `<dl>
		    <dt>${item.title}</dt>
		    <dd class="place">${item.place}</dd>
		    <dd class="award">${item.award}</dd>
		    <dd class="scope">${item.scope}</dd>
		    <dd class="comment">${item.comment}</dd>
	<dd class="paragraph">${item.paragraph}</dd>
		  </dl>`;
    };
    $.getJSON('./ajax/projects.json', function(data) {
      var headersSet;
      headersSet = new Set();
      $.each(data.projects, function(key, project) {
        if (!(headersSet.has(project.header))) {
          headersSet.add(project.header);
          panels.projects.append(`<h2 id=${project.header}>${project.header}</h2>`);
          $('.projects nav.sidenav ul').append(`<li><a data-target=projects href=#${project.header}>${project.header}</a></li>`);
        }
        return panels.projects.append(infoTemplate(project));
      });
      return $('.projects nav.sidenav a').click(function(e) {
        e.preventDefault();
        return scrollTo($(e.target).attr('href'), $(e.target).data('target'));
      });
    });
    $.getJSON('./ajax/about.json', function(data) {
      var headersSet;
      headersSet = new Set();
      $.each(data.about, function(key, article) {
        if (!(headersSet.has(article.header))) {
          headersSet.add(article.header);
          panels.about.append(`<h2 id=${article.header}>${article.header}</h2>`);
          $('.about nav.sidenav ul').append(`<li><a data-target=about href=#${article.header}>${article.header}</a></li>`);
        }
        return panels.about.append(infoTemplate(article));
      });
      return $('.about nav.sidenav a').click(function(e) {
        e.preventDefault();
        return scrollTo($(e.target).attr('href'), $(e.target).data('target'));
      });
    });
    // MAE MENU W PANELU (NAGOWKI H2)
    scrollTo = function(bookmark, target) {
      return $('.layout.' + target).animate({
        scrollTop: $(`h2${bookmark}`).data('pos')
      }, 500);
    };
    // iNFORMACJA O CIASTECZKACH, COOKIE-STUFF
    if (document.cookie.search('cookieConsent=true') >= 0) {
      $('.cookie-stuff').hide();
    }
    cookieConsent = function() {
      var exDate, expires;
      exDate = new Date();
      exDate.setTime(exDate.getTime() + 90 * 24 * 60 * 60 * 1000);
      expires = 'expires=' + exDate.toUTCString();
      return document.cookie = `cookieConsent=true; ${expires}; path=/`;
    };
    return $('.cookie-stuff button').click(function() {
      cookieConsent();
      return $(this).parent().hide();
    });
  });

  window.onload = function() {
    return $('.preloader').fadeOut();
  };

}).call(this);

